import React from 'react'
import CytoscapeComponent from 'react-cytoscapejs'

class TreeMaker extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            nodes: [
                { data: { id: '0', label: 'Root' }, position: { x: 300, y: 300 } }
            ],
            inputs: [],
            width: window.innerWidth,
            height: window.innerHeight,
        };
    }

    componentDidMount() {
        this.cy.nodes().on('tap', (ev, ext) => { 
            console.log(ev);
            const {x, y} = ev.target.position()
            this.setState({
                inputs: [
                    {
                        text: 'Add',
                        x: `${x + 20}px`,
                        y: `${y}px`,
                        onclick: (ev) => {
                            const cur = this.state.nodes
                            const newNode = {data: { id: cur.length, label: 'Any' }, position: { x: x + 50, y: y + 20} }
                            const newEdge = {data: { source: ev.target.data.id, target: cur.length, label: 'Edge' }}
                            cur.push(newNode)
                            cur.push(newEdge)
                            this.setState({nodes: cur})
                        }
                    },
                    {
                        text: 'Delete',
                        x: `${x + 80}px`,
                        y: `${y}px`
                    }
                ]
            })
         })
    }

    render() {
        // const elements = [
        //     { data: { id: 'one', label: 'Node 1' }, position: { x: 300, y: 300 } },
        //     { data: { id: 'two', label: 'Node 2' }, position: { x: 500, y: 500 } },
        //     { data: { source: 'one', target: 'two', label: 'Edge from Node1 to Node2' } }
        // ];
        const elements = this.state.nodes
        
        const inputs = this.state.inputs.map((v) => {
            return <button 
                style={{ position: 'absolute', top: v.y, left: v.x }}
                key={v.text}
                onClick={v.onclick}
            >{v.text}</button>
        })

        return <div>
            <CytoscapeComponent
                elements={elements}
                style={{ width: this.state.width, height: this.state.height }}
                cy={(cy) => { this.cy = cy }}
            />
            {inputs}
        </div>
    }
}

export default TreeMaker